const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			autoplay: true,
			interval: 2000,
			bannerList:[],
			list: [],
			page: '1',
			imgUrl: '',
			vip: '',
			isShow: false,
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
	},
	onShow() {
		this.getAd()
		this.getList()
	},
	methods: {
		// 加载轮播图
		getAd() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/board/info`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					board_id: '5',
					page_size: '10',
					page: 1
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						 this.bannerList = res.data.board_ad.aditems
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		},
		getList() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					board_id: '5',
					page_size: '10',
					page: this.page
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						 this.list = this.list.concat(res.data.data)
						 if(res.data.data == 0){
						 	uni.showToast({
						 		title:'暂无更多数据',
						 		icon:"none"
						 	})
						 }
						 this.isShow = false
					} else if(res.data.status_code == 202) {
						// this.vip = res.data.message
						this.isShow = true
					}else{
						if(res.data.message == '用户不存在或登陆已过期'){
							uni.showToast({
								title: '未检测到用户的登录记录，请进行登录',
								icon: 'none',
								duration: 3000
							});
							setTimeout(() => {
								// 进入登录页
								uni.reLaunch({
									url: './login'
								});
							}, 3000)
						}else{
							uni.showToast({
								title: res.data.message,
								icon: 'none',
								duration: 3000
							});
						}
					}
			
				}
			})
		},
		// 跳转详情
		goDetail(e){
			uni.navigateTo({
				url:`/pages/articleDetail?id=${e}`
			})
		},
		onReachBottom() {
			this.page ++;
			this.getList()
		}
	}
}
